'use client';

import React, { useState, useEffect } from 'react';
import Image from 'next/image';
import { Text, Tag } from '../../atoms';
import { ExternalLink } from 'lucide-react';
import { Project } from '@/lib/data';
import {
  ProjectCardWrapper,
  ProjectInfo,
  ProjectNumber,
  ProjectMeta,
  ProjectYear,
  ProjectCategory,
  ProjectTitle,
  ProjectDescription,
  ProjectTags,
  ProjectLink,
  ProjectVisual,
  ProjectDivider,
} from './ProjectCard.styles';

interface ProjectCardProps {
  project: Project;
  index: number;
  isLast?: boolean;
}

export const ProjectCard: React.FC<ProjectCardProps> = ({ project, index, isLast = false }) => {
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const checkMobile = () => {
      setIsMobile(window.innerWidth <= 1024);
    };

    checkMobile();
    window.addEventListener('resize', checkMobile);

    return () => window.removeEventListener('resize', checkMobile);
  }, []);

  const aosProps = isMobile ? {} : {
    'data-aos': index % 2 === 0 ? "fade-right" : "fade-left",
    'data-aos-delay': index * 100
  };

  return (
    <>
      <ProjectCardWrapper $isReverse={index % 2 === 1} {...aosProps}>
        <ProjectInfo>
          <Text variant="h4" as="span">
            <ProjectNumber>{project.number}</ProjectNumber>
          </Text>
          <ProjectMeta>
            <ProjectYear>{project.year}</ProjectYear>
            <span>•</span>
            <ProjectCategory>{project.category}</ProjectCategory>
          </ProjectMeta>
          <Text variant="h3" as="span">
            <ProjectTitle>{project.title}</ProjectTitle>
          </Text>
          <Text variant="body" color="secondary" as="span">
            <ProjectDescription>{project.description}</ProjectDescription>
          </Text>
          <ProjectTags>
            {project.tags.map((tag) => (
              <Tag key={tag}>{tag}</Tag>
            ))}
          </ProjectTags>
          <ProjectLink
            href={project.link || 'https://google.com'}
            target="_blank"
            rel="noopener noreferrer"
          >
            Live Link
            <ExternalLink size={16} />
          </ProjectLink>
        </ProjectInfo>
        <ProjectVisual>
          <Image
            src="/images/ml-jobs.png"
            alt={project.title}
            fill
            style={{ objectFit: 'cover', borderRadius: '0.5rem' }}
          />
        </ProjectVisual>
      </ProjectCardWrapper>
      {!isLast && <ProjectDivider />}
    </>
  );
};

